body,
html {
  width: 100%;
  height: 100%;
  background-color: #000;
  -moz-user-select: none; 
  -webkit-user-select: none; 
  -ms-user-select: none; 
  user-select: none; 
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  background-color: #000;
  width: 100%;
}

#root {
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.clear {
  clear: both;
}

.loading {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: black;
  z-index: 1000;
  .plane {
    width: 80%;
    height: 80%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.no_data{
  width: 100%;
  padding-top: 150px;
  img{
    display: block;
    width: 150px;
    margin: 0 auto;
  }
  .text{
  width: 100%;
  color: #fff;
  margin-top: 20px;
  text-align: center;
    font-size: 24px;
  }
}
.mask{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:100;
  background:rgba(0,0,0,0.5);
}