.statisticWrapper {
    margin-top: 40px;
    padding-bottom: 50px;
    .highcharts-popup {
        display: none!important;
    }
    .statisticTabWrapper {
        background-color: #1a1a1a;
        width: 600px;
        margin: 0 auto;
        border-radius: 15px;
        overflow: hidden;
        height: 60px;
        .taberContainer {
            position: relative;
            box-sizing: border-box;
            height: 60px;
            .taber {
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                display: flex;
                height: 60px;
                box-sizing: border-box;
                .tabItem,
                .tabItemChecked {
                    cursor: pointer;
                    height: 60px;
                    box-sizing: border-box;
                    line-height: 60px;
                    font-size: 28px;
                    font-weight: 400;
                    color: #a0a0a0;
                    text-align: center;
                    width: 33.33%;
                }
                .tabItemChecked {
                    cursor: pointer;
                    color: #aaaaaa;
                }
            }

            .tabBar {
                position: absolute;
                border-radius: 15px;
                height: 60px;
                box-sizing: border-box;
                background-color: #494949;
                border: 1px #494949 solid;
                transition: top 0.3s cubic-bezier(0.35, 0, 0.25, 1), left 0.3s cubic-bezier(0.35, 0, 0.25, 1),
                    color 0.3s cubic-bezier(0.35, 0, 0.25, 1), width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
            }
        }
    }
    .chartWraper {
        margin: 50px 10px 10px 10px;
        #statisticChart {
            margin: 0;
            height: 400px;
            width: 85%;
            margin: 0 auto;
        }
        .statisInfo {
            display: flex;
            width: 85%;
            margin: 0 auto;
            margin-top: 30px;
            margin-bottom: 30px;
            justify-content: space-around;
            .content {
                margin-bottom: 30px;
                color: #a6a6a6;
                font-size: 26px;
                text-align: center;
                .value {
                    font-weight: bold;
                    font-size: 20px;
                }
            }
            .content:last-child {
                margin-bottom: 0;
            }
        }
    }
    .tipsWrapper {
        width: 700px;
        margin: 0 auto;
        margin-top: 40px;
        display: flex;
            justify-content: center;
            .tips {
            width: 220px;
            display: flex;
            align-items: center;
            .line1,
            .line2 {
                width: 30px;
                height: 15px;
            }
            .line1 {
                background-color: #a86c24;
            }
            .line2 {
                background-color: #2476a8;
            }
            .tipsInfo {
                margin-left: 20px;
                color: #afafaf;
                font-size: 18px;
            }
        }
    }
}

.recommendWrapper {
    .title {
        display: flex;
        color: #fff;
        font-size: 24px;
        position: sticky;
        top: 100px;
        background-color: #000;
        height: 100px;
        line-height: 100px;
        .analyst {
            flex: 5;
            margin-left: 90px;
            text-align: left;
        }
        .rate {
            flex: 3;
            text-align: center;
        }
        .price {
            flex: 3;
            text-align: center;
        }
        .date {
            flex: 3;
            text-align: center;
        }
    }
    .content {
        padding: 0;
        margin: 0;
        .item {
            display: flex;
            color: #fff;
            font-size: 22px;
            margin-bottom: 40px;
            line-height: 50px;
            .icon {
                width: 50px;
                height: 50px;
                margin-left: 20px;
                margin-right: 20px;
                background-color: #ceced5;
                font-weight: bold;
                text-align: center;
                border-radius: 25px;
            }
            .analyst {
                flex: 5;
                text-align: left;
                p {
                    margin: 0;
                    line-height: 25px;
                    font-weight: bold;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
                .company {
                    margin-top: 5px;
                    font-size: 14px;
                    color: #9f9f9f;
                    -webkit-line-clamp: 2;
                }
            }
            .rate {
                flex: 3;
                font-size: 24px;
                font-weight: bold;
                text-align: center;
            }
            .price {
                flex: 3;
                font-size: 24px;
                font-weight: bold;
                text-align: center;
            }
            .date {
                flex: 3;
                text-align: center;
            }
        }
    }
}

.LineDataWrapper {
    .tabTitle {
        display: flex;
        color: #fff;
        font-size: 26px;
        position: sticky;
        background-color: #000;
        margin-top: 30px;
        .kline {
            flex: 2;
            margin-left: 130px;
            text-align: center;
        }
        .date {
            flex: 3;
            text-align: center;
        }
    }
    .list {
        margin-top: 20px;
        padding: 0;
        .item {
            display: flex;
            line-height: 100px;
            height: 100px;
            align-items: center;
            padding-left: 20px;
            padding-right: 20px;
            .icon {
                width: 60px;
                height: 60px;
                margin-left: 30px;
                margin-right: 20px;
                border-radius: 10px;
                background-color: #ceced5;
                position: relative;
                img {
                    position: absolute;
                    width: 50px;
                    display: block;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            .title {
                flex: 2;
                color: #a1a1a1;
                font-size: 26px;
                text-align: center;
            }
            .date {
                flex: 3;
                font-size: 24px;
                color: #a1a1a1;
                // color: #018cef;
                text-align: center;
            }
        }
    }
}
